import nodemailer from 'nodemailer'
import get from 'lodash/get'
import genPm from 'wsemi/src/genPm.mjs'
import isstr from 'wsemi/src/isstr.mjs'
import isobj from 'wsemi/src/isobj.mjs'
import isEmail from 'wsemi/src/isEmail.mjs'


/**
 * email
 *
 * @param {*} [opt={}] {}
 * @param {String} [opt.srcName='system by w-email'] 'system by w-email'
 * @param {String} opt.srcEmail email
 * @param {String} opt.srcPW email
 * @param {String} [opt.srcHost='smtp.gmail.com'] 'smtp.gmail.com'
 * @param {String} [opt.srcPort=587] 587
 * @param {String} [opt.emTitle=''] ''
 * @param {String} [opt.emContent=''] ''
 * @param {String|Array} [opt.toEmails=[]] email[]
 * @param {String|Array} [opt.toEmailsCC=[]] email[]
 * @param {String|Array} [opt.toEmailsBCC=[]] email[]
 * @param {Object|Array} [opt.emAttachments=[]] []filenamepath
 * @returns {Promise} Promiseresolvereject
 * @example
 * import WEmail from 'w-eamil'
 *
 * //opt
 * let opt = {
 *     srcName: 'test name',
 *     srcEmail: 'your email',
 *     srcPW: 'your password',
 *     emTitle: 'test title',
 *     emContent: '<h4>test head</h4>'+'<div>test content</div>',
 *     toEmails: 'to email',
 *     //toEmailsCC: 'to email',
 *     //toEmailsBCC: 'to email',
 *     emAttachments: {
 *         filename: 'your filename',
 *         path: 'your filepath'
 *     }
 * }
 *
 * //WEmail
 * new WEmail(opt)
 *     .then((msg)=>{
 *         console.log('then',msg)
 *     })
 *     .catch((err)=>{
 *         console.log('catch',err)
 *     })
 */
function WEmail(opt = {}) {
    let pm = genPm()

    //
    let srcName = get(opt, 'srcName', 'system by w-email')

    //email
    let srcEmail = get(opt, 'srcEmail', '')
    if (!isEmail(srcEmail)) {
        pm.reject('srcEmail is not email')
        return pm
    }

    //email
    let srcPW = get(opt, 'srcPW', '')

    //
    let srcHost = get(opt, 'srcHost', 'smtp.gmail.com')

    //
    let srcPort = get(opt, 'srcPort', 587)

    //
    let emTitle = get(opt, 'emTitle', '')

    //
    let emContent = get(opt, 'emContent', '')

    //
    let emAttachments = get(opt, 'emAttachments', [])
    if (isobj(emAttachments)) {
        emAttachments = [emAttachments]
    }

    //email
    let toEmails = get(opt, 'toEmails', [])
    if (isstr(toEmails)) {
        toEmails = [toEmails]
    }

    //email
    let toEmailsCC = get(opt, 'toEmailsCC', [])
    if (isstr(toEmailsCC)) {
        toEmailsCC = [toEmailsCC]
    }

    //email
    let toEmailsBCC = get(opt, 'toEmailsBCC', [])
    if (isstr(toEmailsBCC)) {
        toEmailsBCC = [toEmailsBCC]
    }

    //transporter
    let transporter = nodemailer.createTransport({
        host: srcHost,
        secureConnecton: true,
        port: srcPort,
        auth: {
            user: srcEmail,
            pass: srcPW
        }
    })

    //optt
    let optt = {
        from: `${srcName} <${srcEmail}>`,
        to: toEmails.join(','),
        cc: toEmailsCC.join(','),
        bcc: toEmailsBCC.join(','),
        subject: emTitle,
        html: emContent,
        attachments: emAttachments
    }

    //sendMail
    transporter.sendMail(optt, function(err, info) {
        if (err) {
            pm.reject(err)
        }
        else {
            pm.resolve(info.response)
        }
    })

    return pm
}


export default WEmail
