import each from 'lodash/each'
import isstr from './isstr.mjs'
import isestr from './isestr.mjs'
import iseobj from './iseobj.mjs'
import replace from './replace.mjs'


/**
 * c{key}okeyvaluevalue
 *
 * Unit Test: {@link https://github.com/yuda-lyu/wsemi/blob/master/test/replaceObj.test.js Github}
 * @memberOf wsemi
 * @param {String} c 
 * @param {Object} o key,value
 * @returns {String} 
 * @example
 * replaceObj('1.25abc', { '5a': '0', '': '' })
 * // => '1.20bc'
 */
function replaceObj(c, o) {

    //check
    if (!isestr(c)) {
        return ''
    }
    if (!iseobj(o)) {
        return ''
    }

    //check obj value(replace string)
    let b = false
    each(o, function(v, k) {
        if (!isstr(v)) { //
            b = true
        }
    })
    if (b) {
        return ''
    }

    each(o, function(v, k) {
        c = replace(c, k, v)
    })

    return c
}


export default replaceObj
