import isarr from './isarr.mjs'
import iser from './iser.mjs'


/**
 * 1
 *
 * Unit Test: {@link https://github.com/yuda-lyu/wsemi/blob/master/test/isearr.test.js Github}
 * @memberOf wsemi
 * @param {*} v 
 * @returns {Boolean} 
 * @example
 * isearr([{}])
 * // => false
 *
 * isearr([{ a: 123 }])
 * // => true
 */
function isearr(v) {

    //check
    if (!isarr(v)) {
        return false
    }

    //check length
    if (v.length === 0) {
        return false
    }

    //check length=1
    if (v.length === 1) {
        if (iser(v[0])) {
            return false
        }
    }

    return true
}


export default isearr
