import verifyWebLogo from './verifyWebLogo.mjs'


function toStrDate(d) {
    return d.format('YYYY-MM-DDTHH:mm:ssZ')
}


function fromStrDate(c) {
    return dayjs(c, 'YYYY-MM-DDTHH:mm:ssZ')
}


function getSuccess(msg = null) {
    return {
        state: 'success',
        msg: msg,
    }
}


function getError(msg = null) {
    return {
        state: 'error',
        msg: msg,
    }
}


//defRole
let defRole = {
    roleSystem: 'system',
    roleGeneral: 'general',
}


//defActive
let defActive = {
    activeYes: 'yes',
    activeNo: 'no',
}


//defExclude
let defExclude = ['<', '>']


/**
 * @class WUserServer::Private
 */


/**
 * hapi
 *
 * @class
 * @param {Object} [opt={}] {}
 * @param {String} [opt.mongoUrl='mongodb://127.0.0.1:27017'] 'mongodb://127.0.0.1:27017'
 * @param {String} [opt.mongoDb='wuser'] 'wuser'
 * @param {String} [opt.mongoCl='users'] 'users'
 * @param {Integer} [opt.port=8080] Hapiport8080
 * @param {String} [opt.apiName='api'] Hapiapi'api'
 * @param {String} [opt.authUrl='http://localhost:8080/auth'] Hapiapiauth'http://localhost:8080/auth'
 * @param {String} [opt.salt=''] ''
 * @param {Integer} [opt.timeTokenExp=30] token30()
 * @param {String} [opt.webName=''] email''
 * @param {String} [opt.webUrl=''] email''
 * @param {String} [opt.webDescription=''] email''
 * @param {String} [opt.emSenderName='WUserServer'] 'WUserServer'
 * @param {String} opt.emSenderEmail email
 * @param {String} [opt.emSenderPW=''] email''
 * @param {String} [opt.emBCC=''] email''
 * @param {String} [opt.emLetterHtml='file:emLetterHtml.mjs'] email'emLetterHtml.mjs'
 * @param {String} [opt.emLetterTeamMessage='Global-User International Company'] email'Global-User International Company'
 * @param {String} [opt.emLetterDoNotReplayMessage='This letter is automatically sent by the system, please do not reply to this email.'] email'This letter is automatically sent by the system, please do not reply to this email.'
 * @param {Array} [opt.emLetterLinks=[]] emailnameurl[]
 * @param {String} [opt.emSignUpTitle='Verification letter'] email'Verification letter'
 * @param {String} [opt.emSignUpHtml='file:emSignUpHtml.mjs'] email html'emSignUpHtml.mjs'
 * @param {String} [opt.emResetPWTitle='Password reset letter'] email'Password reset letter'
 * @param {String} [opt.emResetPWHtml='file:emResetPWHtml.mjs'] email html'emResetPWHtml.mjs'
 * @param {String} [opt.verifyWebHtml='file:verifyWebHtml.mjs'] html'verifyWebHtml.mjs'
 * @param {String} [opt.verifyWebLogo='file:verifyWebLogo.mjs'] logo'verifyWebLogo.mjs'svg
 * @param {String} [opt.verifyWebMsgSuccess='Verify successfully'] 'Verify successfully'
 * @param {String} [opt.verifyWebMsgExpired='Code has expired'] 'Code has expired'
 * @param {String} [opt.verifyWebMsgCount='Go to the website automatically after {s} seconds...'] 'Go to the website automatically after {s} seconds..Promise.'
 * @param {Object} [opt.funcs={}] {}
 * @param {Array} [opt.routes=[]] hapi routes[]
 * @returns {Object} 
 * @example
 * import WUserServer from 'WUserServer/src/w-user-server.mjs'
 *
 *
 * //webName
 * let webName = 'User management system'
 * // let webName = ''
 *
 *
 * //webUrl
 * let webUrl = 'https://google.com'
 *
 *
 * //webDescription
 * let webDescription = "Let's do something and make it easier"
 * // let webDescription = ''
 *
 *
 * //opt
 * let opt = {
 *     mongoUrl: 'mongodb://username:password@127.0.0.1:27017',
 *     mongoDb: 'wuser',
 *     mongoCl: 'users',
 *     port: 8080,
 *     apiName: 'api',
 *     authUrl: 'http://localhost:8080/auth', //need webUrl+'/'+auth, use 'http://localhost:8080' in test case
 *     salt: 'zLAUfSLDUuausd0Aasu912SDU', //generate it for sites
 *     timeTokenExp: 30,
 *
 *     emSenderName: webName,
 *     emSenderEmail: "sender's email", //email address for email sender
 *     emSenderPW: "sender's password", //password for email sender
 *
 *     webName: webName,
 *     webUrl: webUrl,
 *     webDescription: webDescription,
 *
 *     // emLetterTeamMessage: `${webName} `,
 *     // emLetterDoNotReplayMessage: '',
 *     emLetterLinks: [{ name: 'Google', url: 'https://google.com' }],
 *
 *     // emSignUpTitle: '',
 *     // emSignUpHtml: `<p> {name} </p><p></p><p><a href="{urlCheckCode}" target="_blank"></a></p>`,
 *
 *     // emResetPWTitle: '',
 *     // emResetPWHtml: `<p> {name} </p><p></p><p><a href="{urlCheckCode}" target="_blank"></a></p>`,
 *
 *     // verifyWebMsgSuccess: '',
 *     // verifyWebMsgExpired: '',
 *     // verifyWebMsgCount: '{s} ',
 *
 *     funcs: {},
 *     routes: [],
 * }
 *
 *
 * //new
 * let wus = new WUserServer(opt)
 * wus.on('all', function({ eventName, data }) {
 *     console.log('all:' + eventName, ...data)
 * })
 */
function WUserServer(opt = {}) {


    //ee
    let ee = new events.EventEmitter()


    //eeEmit
    function eeEmit(name, ...args) {
        setTimeout(() => {
            ee.emit(name, ...args)
            ee.emit('all', { eventName: name, data: [...args] })
        }, 1)
    }


    //pmWithEmit
    function pmWithEmit(fn, eventName = null) {
        return function() {
            let pm = genPm()

            //args
            let args = cloneDeep(arguments) //, , thencatch, 

            function core(msg) {
                if (eventName) {
                    eeEmit(eventName, {
                        input: args,
                        output: msg,
                    })
                }
            }

            fn.apply(this, arguments)
                .then(function(msg) {
                    core(msg)
                    pm.resolve(msg)
                })
                .catch(function(err) {
                    core(err)
                    pm.resolve(err)
                })

            return pm
        }
    }
    // function pmWithEmit(f, eventName = null) {
    //     return wrap(f, function(func, ...args) {
    //         let pm = genPm()

    //         function core(msg) {
    //             if (eventName) {
    //                 eeEmit(eventName, {
    //                     input: [...args],
    //                     output: msg,
    //                 })
    //             }
    //         }

    //         func(...args)
    //             .then(function(msg) {
    //                 core(msg)
    //                 pm.resolve(msg)
    //             })
    //             .catch(function(err) {
    //                 core(err)
    //                 pm.resolve(err)
    //             })

    //         return pm
    //     })
    // }


    //cloneDeep
    opt = cloneDeep(opt)


    //default
    if (!isestr(opt.mongoUrl)) {
        opt.mongoUrl = 'mongodb://127.0.0.1:27017'
    }
    if (!isestr(opt.mongoDb)) {
        opt.mongoDb = 'wuser'
    }
    if (!isestr(opt.mongoCl)) {
        opt.mongoCl = 'users'
    }
    if (!ispint(opt.port)) {
        opt.port = 8080
    }
    if (!isestr(opt.apiName)) {
        opt.apiName = 'api'
    }
    if (!isestr(opt.authUrl)) {
        opt.authUrl = 'http://localhost:8080/auth'
    }
    if (!isestr(opt.salt)) {
        opt.salt = '' //salt for password
    }
    if (!ispint(opt.timeTokenExp)) {
        opt.timeTokenExp = 30
    }
    if (!isestr(opt.webName)) {
        opt.webName = ''
    }
    if (!isestr(opt.webUrl)) {
        opt.webUrl = ''
    }
    if (!isestr(opt.webDescription)) {
        opt.webDescription = ''
    }
    if (!isestr(opt.emSenderName)) {
        opt.emSenderName = 'WUserServer'
    }
    if (!isestr(opt.emSenderEmail)) {
        console.log('invalid emSenderEmail')
        return ee
    }
    if (!isestr(opt.emSenderPW)) {
        opt.emSenderPW = ''
    }
    if (!isestr(opt.emBCC)) {
        opt.emBCC = ''
    }
    if (!isestr(opt.emLetterHtml)) {
        opt.emLetterHtml = emLetterHtml
    }
    if (!isestr(opt.emLetterTeamMessage)) {
        opt.emLetterTeamMessage = 'Global-User International Company'
    }
    if (!isestr(opt.emLetterDoNotReplayMessage)) {
        opt.emLetterDoNotReplayMessage = 'This letter is automatically sent by the system, please do not reply to this email.'
    }
    if (!isarr(opt.emLetterLinks)) {
        opt.emLetterLinks = []
    }
    if (!isestr(opt.emSignUpTitle)) {
        opt.emSignUpTitle = 'Verification letter'
    }
    if (!isestr(opt.emSignUpHtml)) {
        opt.emSignUpHtml = emSignUpHtml
    }
    if (!isestr(opt.emResetPWTitle)) {
        opt.emResetPWTitle = 'Password reset letter'
    }
    if (!isestr(opt.emResetPWHtml)) {
        opt.emResetPWHtml = emResetPWHtml
    }
    if (!isestr(opt.verifyWebHtml)) {
        opt.verifyWebHtml = verifyWebHtml
    }
    if (!isestr(opt.verifyWebLogo)) {
        opt.verifyWebLogo = verifyWebLogo
    }
    if (!isestr(opt.verifyWebMsgSuccess)) {
        opt.verifyWebMsgSuccess = 'Verify successfully'
    }
    if (!isestr(opt.verifyWebMsgExpired)) {
        opt.verifyWebMsgExpired = 'Code has expired'
    }
    if (!isestr(opt.verifyWebMsgCount)) {
        opt.verifyWebMsgCount = 'Go to the website automatically after {s} seconds...'
    }
    if (!isobj(opt.funcs)) {
        opt.funcs = {}
    }
    if (!isarr(opt.routes)) {
        opt.routes = []
    }


    //worm
    let worm = WOrm({
        url: opt.mongoUrl,
        db: opt.mongoDb,
        cl: opt.mongoCl,
    })


    //schema
    let sc = {
        id: {
            type: 'String',
            public: true,
            necessary: true,
            exclude: [],
            canModify: false,
        },
        name: {
            type: 'String',
            public: true,
            necessary: false,
            exclude: defExclude,
            canModify: true,
        },
        pwEnc: {
            type: 'String',
            public: false,
            necessary: true,
            exclude: [],
            canModify: false,
        },
        email: {
            type: 'String',
            public: true,
            necessary: true,
            exclude: defExclude,
            canModify: true,
        },
        address: {
            type: 'String',
            public: true,
            necessary: false,
            exclude: defExclude,
            canModify: true,
        },
        phone: {
            type: 'String',
            public: true,
            necessary: false,
            exclude: defExclude,
            canModify: true,
        },
        organization: {
            type: 'String',
            public: true,
            necessary: false,
            exclude: defExclude,
            canModify: true,
        },
        position: {
            type: 'String',
            public: true,
            necessary: false,
            exclude: defExclude,
            canModify: true,
        },
        role: {
            type: 'String', //roleGeneral|roleSystem
            public: true,
            necessary: true,
            exclude: defExclude,
            canModify: false,
        },
        checkCode: {
            type: 'String',
            public: false,
            necessary: false,
            exclude: [],
            canModify: false,
        },
        token: {
            type: 'String',
            public: false,
            necessary: false,
            exclude: [],
            canModify: false,
        },
        tokenExp: {
            type: 'String',
            public: false,
            necessary: false,
            exclude: [],
            canModify: false,
        },
        timeCreate: {
            type: 'String',
            public: false,
            necessary: true,
            exclude: [],
            canModify: false,
        },
        timeLogin: {
            type: 'String',
            public: false,
            necessary: false,
            exclude: [],
            canModify: false,
        },
        data: {
            type: 'Object',
            public: false,
            necessary: false,
            exclude: [],
            canModify: false,
        },
        active: {
            type: 'String', //activeYes|activeNo
            public: false,
            necessary: true,
            exclude: [],
            canModify: false,
        },
    }


    let wsaveR = pm2resolve(worm.save)


    function verifyPropCore(k, v) {
        if (haskey(sc, k)) {
            let t = sc[k].type
            let e = sc[k].exclude
            if (t === 'String') {
                if (!isstr(v)) {
                    return getError('value is not string: ' + v)
                }
                if (binstr(v, e)) {
                    return getError('can not contain special characters: "<" or ">"')
                }
                return getSuccess()
            }
            else if (t === 'Object') {
                if (!isobj(v)) {
                    return getError('value is not object: ' + v)
                }
                return getSuccess()
            }
            return getError('type is not defined: ' + t)
        }
        else {
            return getError('invalid key: ' + k)
        }
    }


    /**
     * 
     *
     * @param {Object} u 
     * @returns {Object} state'success'state'error'msg
     */
    function verifyProp(u) {
        each(u, function(v, k) {
            let r = verifyPropCore(k, v)
            if (r.state === 'error') {
                return r
            }
        })
        return getSuccess()
    }


    /**
     * 
     *
     * @param {Object} u 
     * @returns {Object} 
     */
    function defaultProp(u) {
        each(sc, function(v, k) {
            let def = ''
            if (v.type === 'Object') {
                def = {}
            }
            if (!haskey(u, k)) {
                u[k] = def
            }
            else if (u[k] === null || u[k] === undefined) {
                u[k] = def
            }
        })
        return u
    }


    async function selectPublic({ token, find }) {
        let pm = genPm()
        let r = null

        //isValidTokenR
        r = await isValidTokenR(token)

        //cehck
        if (r.state === 'error') {
            pm.reject(r.msg)
            return pm
        }

        //select
        worm.select(find)
            .then(function(msg) {
                if (msg.length >= 1) {

                    //uesrs
                    let users = msg

                    //cols, 
                    let cols = []
                    each(sc, function(v, k) {
                        if (v.public) {
                            cols.push(k)
                        }
                    })

                    //user, 
                    users = map(users, function(u) {
                        return pick(u, cols)
                    })

                    pm.resolve(users) //, 
                }
                else {
                    pm.reject('can not find user')
                }
            })
            .catch(function(err) {
                console.log('selectPublic catch:', err, 'find:', find)
                pm.reject('error')
            })

        return pm
    }
    let selectPublicR = pm2resolve(selectPublic)


    /**
     * 
     *
     * @memberof WUserServer::Private
     * @function insert
     * @param {Object} user 
     * @returns {Promise} Promiseresolveid(checkCode)reject
     */
    async function insert(user) {
        let pm = genPm()
        let r = null

        //check, 
        if (!isestr(user.pwEnc)) {
            pm.reject('invalid password')
            return pm
        }

        //check, email
        if (!isEmail(user.email)) {
            pm.reject('invalid email')
            return pm
        }

        //check email
        let existEmail = await isExist({ email: user.email })
        if (existEmail) {
            pm.reject('email has been used')
            return pm
        }

        //verifyProp
        r = verifyProp(user)
        if (r.state === 'error') {
            pm.reject(r.msg)
            return pm
        }

        //defaultProp
        user = defaultProp(user)

        //id
        user.id = genID()

        //
        if (opt.salt !== '') {
            user.pwEnc = str2sha512(user.pwEnc + opt.salt)
        }

        //checkCode
        user.checkCode = genID()

        //timeCreate
        user.timeCreate = now2str()

        //role
        user.role = defRole.roleGeneral

        //active
        user.active = defActive.activeYes

        //insert
        worm.insert(user)
            .then(function(msg) {
                pm.resolve({
                    id: user.id,
                    checkCode: user.checkCode,
                })
            })
            .catch(function(err) {
                console.log('insert catch:', err, 'user:', user)
                pm.reject('error')
            })

        return pm
    }
    let insertR = pm2resolve(insert)


    /**
     * 
     *
     * @memberof WUserServer::Private
     * @function selectOne
     * @param {Object} find 
     * @returns {Promise} Promiseresolvereject
     */
    function selectOne(find) {
        let pm = genPm()

        //select
        worm.select(find)
            .then(function(msg) {
                if (msg.length === 1) {
                    pm.resolve(msg[0])
                }
                else if (msg.length > 1) {
                    pm.reject(`find ${msg.length} users`)
                }
                else {
                    pm.reject('can not find user')
                }
            })
            .catch(function(err) {
                console.log('selectOne catch:', err, 'find:', find)
                pm.reject('error')
            })

        return pm
    }
    let selectOneR = pm2resolve(selectOne)


    /**
     * 
     *
     * @memberof WUserServer::Private
     * @function isExist
     * @param {Object} find 
     * @returns {Promise} Promiseresolvereject
     */
    function isExist(find) {
        let pm = genPm()

        //check
        if (!iseobj(find)) {
            pm.reject('invalid find')
            return pm
        }

        //select
        worm.select(find)
            .then(function(msg) {
                if (msg.length >= 1) { //1, 
                    pm.resolve(true)
                }
                else {
                    pm.resolve(false)
                }
            })
            .catch(function(err) {
                console.log('select catch:', err, 'find:', find)
                pm.reject('error')
            })

        return pm
    }


    async function signUp(user) {
        let pm = genPm()
        let r = null

        //insertR
        r = await insertR(user)

        //cehck
        if (r.state === 'error') {
            pm.reject(r.msg) //
            return pm
        }

        //user
        let id = r.msg.id
        user.id = id

        //checkCode
        let checkCode = r.msg.checkCode
        user.checkCode = checkCode

        //urlCheckCode
        let urlCheckCode = `${opt.authUrl}?checkCode=${checkCode}`

        //sendEmailWhenSignUpR, (checkCode), 
        r = await sendEmailWhenSignUpR(user.name, urlCheckCode, user.email)

        //cehck
        if (r.state === 'error') {
            console.log('signUp catch:', r.msg, 'emailObj:', { name: user.name, urlCheckCode: urlCheckCode, email: user.email })
            pm.reject('can not send email')
            return pm
        }

        pm.resolve(user.id)
        return pm
    }
    let signUpR = pm2resolve(signUp)


    async function logIn({ email, pwEnc }) {
        let pm = genPm()
        let r = null

        //check
        if (!isestr(email)) {
            pm.reject('invalid email')
            return pm
        }
        if (!isestr(pwEnc)) {
            pm.reject('invalid password')
            return pm
        }

        //find
        let find = { email } //email

        //selectOneR
        r = await selectOneR(find)

        //cehck
        if (r.state === 'error') {
            console.log('logIn catch:', r.msg, 'find:', find)
            pm.reject('email or password is incorrect') //, email
            return pm
        }

        //user
        let user = r.msg

        //check checkCode, , , 
        if (user.checkCode !== '') {
            pm.reject('account is not verified')
            return pm
        }

        //
        if (opt.salt !== '') {
            pwEnc = str2sha512(pwEnc + opt.salt)
        }

        //check pwEnc, 
        if (user.pwEnc !== pwEnc) {
            pm.reject('email or password is incorrect') //, email
            return pm
        }

        //check active, 
        if (user.active !== defActive.activeYes) {
            pm.reject('account is suspended')
            return pm
        }

        //isValidTokenR
        r = await isValidTokenR(user.token)

        //cehck
        if (r.state === 'success') {

            pm.resolve(user.token) //token
            return pm
        }

        //token
        let token = genID()

        //userModify
        let userModify = {
            id: user.id,
            token,
            tokenExp: toStrDate(dayjs().add(opt.timeTokenExp, 'minute')),
            timeLogin: now2str()
        }

        //wsaveR
        r = await wsaveR(userModify)

        //cehck
        if (r.state === 'error') {
            console.log('logIn catch:', r.msg, 'userModify:', userModify)
            pm.reject('can not save token')
            return pm
        }

        pm.resolve(token)
        return pm
    }
    let logInR = pm2resolve(logIn)


    async function logOut(token) {
        let pm = genPm()
        let r = null

        //check
        if (!isestr(token)) {
            pm.reject('invalid token')
            return pm
        }

        //find
        let find = { token }

        //selectOneR
        r = await selectOneR(find)

        //cehck
        if (r.state === 'error') {
            console.log('logOut catch:', r.msg, 'find:', find)
            pm.reject('invalid token')
            return pm
        }

        //user
        let user = r.msg

        //userModify
        let userModify = {
            id: user.id,
            token: '',
            tokenExp: ''
        }

        //wsaveR
        r = await wsaveR(userModify)

        //cehck
        if (r.state === 'error') {
            console.log('logOut catch:', r.msg, 'userModify:', userModify)
            pm.reject('can not update token')
            return pm
        }

        pm.resolve('success')
        return pm
    }
    let logOutR = pm2resolve(logOut)


    /**
     * 
     *
     * @memberof WUserServer::Private
     * @function verifyCheckCode
     * @param {String} checkCode
     * @returns {Promise} Promiseresolveidreject
     */
    function verifyCheckCode(checkCode) {
        let pm = genPm()

        //check
        if (!isestr(checkCode)) {
            pm.reject(opt.verifyWebMsgExpired)
            return pm
        }

        //find
        let find = { checkCode }

        //selectOne
        selectOne(find)
            .then(function(user) {

                //userModify
                let userModify = {
                    id: user.id,
                    checkCode: ''
                }

                //save
                return worm.save(userModify)
            })
            .then(function(msg) {
                pm.resolve(opt.verifyWebMsgSuccess)
            })
            .catch(function(err) {
                console.log('verifyCheckCode catch:', err, 'find:', find)
                pm.reject(opt.verifyWebMsgExpired)
            })

        return pm
    }


    /**
     * html
     *
     * @memberof WUserServer::Private
     * @function genVerifyHtml
     * @param {String} [rdMessage=''] 
     * @param {String} [rdCountdown='false'] 'true''false'
     * @param {String} [rdCountdownMessage=''] 
     * @returns {String} html
     */
    function genVerifyHtml(rdMessage = '', rdCountdown = 'false', rdCountdownMessage = '') {

        //tmp
        let tmp = opt.verifyWebHtml

        //replaceObj
        tmp = replaceObj(tmp, {
            '{rdWebName}': opt.webName,
            '{rdWebDescription}': opt.webDescription,
            '{rdWebUrl}': opt.webUrl,
            '{rdLogo}': opt.verifyWebLogo,
            '{rdMessage}': rdMessage, //|
            '{rdCountdown}': rdCountdown, //true|false
            '{rdCountdownMessage}': rdCountdownMessage, //
        })

        return tmp
    }


    function verifyCheckCodeAndGetHtml(checkCode) {
        let pm = genPm()

        //rdCountdownMessage
        let rdCountdownMessage = opt.verifyWebMsgCount

        verifyCheckCode(checkCode)
            .then(function(msg) {
                eeEmit('verifyCheckCodeAndGetHtml', { state: 'success', msg: msg, checkCode })
                pm.resolve(genVerifyHtml(msg, 'true', rdCountdownMessage))
            })
            .catch(function(err) {
                eeEmit('verifyCheckCodeAndGetHtml', { state: 'error', msg: err, checkCode })
                pm.resolve(genVerifyHtml(err, 'false', ''))
            })

        return pm
    }


    /**
     * 
     *
     * @memberof WUserServer::Private
     * @function verifyTokenExp
     * @param {String} tokenExp
     * @returns {Promise} Promiseresolvereject
     */
    function verifyTokenExp(tokenExp) {
        //promise, 
        let pm = genPm()

        //check
        if (!istimeTZ(tokenExp)) {
            pm.reject('no expire time for token')
        }
        else {

            //
            let dnow = dayjs()

            //token
            let dtoken = fromStrDate(tokenExp)

            //
            let iMinutes = dtoken.diff(dnow, 'minute')

            //check
            if (iMinutes > 0) {
                pm.resolve('success')
            }
            else if (iMinutes > opt.timeTokenExp) {
                console.log('verifyTokenExp error:', 'iMinutes=' + iMinutes, '>', 'timeTokenExp=' + opt.timeTokenExp)
                pm.reject('invalid tokenExp')
            }
            else {
                pm.reject('token expired')
            }

        }

        return pm
    }
    let verifyTokenExpR = pm2resolve(verifyTokenExp)


    async function isValidToken(token) {
        let pm = genPm()
        let r = null

        //check
        if (!isestr(token)) {
            pm.reject('invalid token')
            return pm
        }

        //find
        let find = { token }

        //selectOneR
        r = await selectOneR(find)

        //cehck
        if (r.state === 'error') {
            console.log('isValidToken catch:', r.msg, 'find:', find)
            pm.reject('invalid token')
            return pm
        }

        //user
        let user = r.msg

        //verifyTokenExpR
        r = verifyTokenExpR(user.tokenExp)

        //cehck
        if (r.state === 'error') {
            pm.reject(r.msg)
            return pm
        }

        pm.resolve('success')
        return pm
    }
    let isValidTokenR = pm2resolve(isValidToken)


    async function refreshTokenExp(token) {
        let pm = genPm()
        let r = null

        //check
        if (!isestr(token)) {
            pm.reject('invalid token')
            return pm
        }

        //find
        let find = { token }

        //selectOneR
        r = await selectOneR(find)

        //cehck
        if (r.state === 'error') {
            console.log('refreshTokenExp catch:', r.msg, 'find:', find)
            pm.reject('invalid token')
            return pm
        }

        //user
        let user = r.msg

        //verifyTokenExp
        r = verifyTokenExp(user.tokenExp)
        if (r.state === 'error') {
            pm.reject(r.err)
            return pm
        }

        //userModify
        let userModify = {
            id: user.id,
            tokenExp: toStrDate(dayjs().add(opt.timeTokenExp, 'minute')),
        }

        //wsaveR
        r = await wsaveR(userModify)

        //cehck
        if (r.state === 'error') {
            console.log('refreshTokenExp catch:', r.msg, 'userModify:', userModify)
            pm.reject('can not update token')
            return pm
        }

        pm.resolve('success')
        return pm
    }
    let refreshTokenExpR = pm2resolve(refreshTokenExp)


    /**
     * token
     *
     * @memberof WUserServer::Private
     * @function getUserFromToken
     * @param {String} token
     * @returns {Promise} Promiseresolvereject
     */
    async function getUserFromToken(token) {
        let pm = genPm()
        let r = null

        //find
        let find = { token }

        //selectOneR
        r = await selectOneR(find)

        //cehck
        if (r.state === 'error') {
            console.log('getUserFromToken catch:', r.msg, 'find:', find)
            pm.reject('invalid token')
            return pm
        }

        //user
        let user = r.msg

        pm.resolve(user)
        return pm
    }

    async function changePW({ email, pwEnc, pwEncNew }) {
        let pm = genPm()
        let r = null

        //check
        if (!isestr(email)) {
            pm.reject('invalid email')
            return pm
        }
        if (!isestr(pwEnc)) {
            pm.reject('invalid old password')
            return pm
        }
        if (!isestr(pwEncNew)) {
            pm.reject('invalid new password')
            return pm
        }

        //find
        let find = { email } //email

        //selectOneR
        r = await selectOneR(find)

        //cehck
        if (r.state === 'error') {
            console.log('changePW catch:', r.msg, 'find:', find)
            pm.reject('email or password is incorrect') //, email
            return pm
        }

        //user
        let user = r.msg

        //check checkCode, 
        if (user.checkCode !== '') {
            pm.reject('account is not verified')
            return pm
        }

        //check active, 
        if (user.active !== defActive.activeYes) {
            pm.reject('account is suspended')
            return pm
        }

        //
        if (opt.salt !== '') {
            pwEnc = str2sha512(pwEnc + opt.salt)
            pwEncNew = str2sha512(pwEncNew + opt.salt)
        }

        //check pwEnc, 
        if (user.pwEnc !== pwEnc) {
            pm.reject('email or password is incorrect') //email
            return pm
        }

        //userModify
        let userModify = {
            id: user.id,
            pwEnc: pwEncNew
        }

        //wsaveR
        r = await wsaveR(userModify)

        //cehck
        if (r.state === 'error') {
            console.log('changePW catch:', r.msg, 'userModify:', userModify)
            pm.reject('can not update password')
            return pm
        }

        pm.resolve('success')
        return pm
    }
    let changePWR = pm2resolve(changePW)


    async function resetPW({ email, pwEncNew }) {
        let pm = genPm()
        let r = null

        //check
        if (!isestr(email)) {
            pm.reject('invalid email')
            return pm
        }
        if (!isestr(pwEncNew)) {
            pm.reject('invalid new password')
            return pm
        }

        //find
        let find = { email } //email

        //selectOneR
        r = await selectOneR(find)

        //cehck
        if (r.state === 'error') {
            console.log('resetPW catch:', r.msg, 'find:', find)
            pm.reject('email is incorrect')
            return pm
        }

        //user
        let user = r.msg

        //check checkCode, 

        //check active, 
        if (user.active !== defActive.activeYes) {
            pm.reject('account is suspended')
            return pm
        }

        //
        if (opt.salt !== '') {
            pwEncNew = str2sha512(pwEncNew + opt.salt)
        }

        //checkCode
        let checkCode = genID()

        //userModify
        let userModify = {
            id: user.id,
            pwEnc: pwEncNew,
            checkCode
        }

        //wsaveR
        r = await wsaveR(userModify)

        //cehck
        if (r.state === 'error') {
            console.log('resetPW catch:', r.msg, 'userModify:', userModify)
            pm.reject('can not update password')
            return pm
        }

        //urlCheckCode
        let urlCheckCode = `${opt.authUrl}?checkCode=${checkCode}`

        //sendEmailWhenResetPWR, (checkCode), 
        r = await sendEmailWhenResetPWR(user.name, urlCheckCode, user.email)

        //cehck
        if (r.state === 'error') {
            console.log('resetPW catch:', r.msg, 'emailObj:', { name: user.name, urlCheckCode: urlCheckCode, email: user.email })
            pm.reject('can not send email')
            return pm
        }

        pm.resolve('success')
        return pm
    }
    let resetPWR = pm2resolve(resetPW)


    async function modifyInfor({ token, user }) {
        let pm = genPm()
        let r = null

        //isValidTokenR
        r = await isValidTokenR(token)

        //cehck
        if (r.state === 'error') {
            pm.reject(r.msg)
            return pm
        }

        //verifyProp
        r = verifyProp(user)
        if (r.state === 'error') {
            pm.reject(r.msg)
            return pm
        }

        //getUserFromToken
        let userOld = await getUserFromToken(token)

        //userModify
        let userModify = {
            id: userOld.id,
        }

        //modify
        let bModify = false
        each(sc, function(v, k) {
            if (v.canModify) {
                if (user[k] && userOld[k] !== user[k]) {
                    bModify = true
                    userModify[k] = user[k]
                }
            }
        })

        //check
        if (!bModify) {
            pm.resolve('no change')
            return pm
        }

        //wsaveR
        r = await wsaveR(userModify)

        //cehck
        if (r.state === 'error') {
            console.log('modifyInfor catch:', r.msg, 'userModify:', userModify)
            pm.reject('can not modify')
            return pm
        }

        pm.resolve('success')
        return pm
    }
    let modifyInforR = pm2resolve(modifyInfor)


    /**
     * email
     *
     * @param {String} emTitle 
     * @param {String} emContent 
     * @param {String|Array} toEmails email
     * @param {String|Array} [toEmailsCC=[]] email[]
     * @param {String|Array} [toEmailsBCC=[]] email[]
     * @returns {Promise} Promiseresolvereject
     */
    function sendEmail(emTitle, emContent, toEmails, toEmailsCC = [], toEmailsBCC = []) {

        //optEmail
        let optEmail = {
            srcName: opt.emSenderName,
            srcEmail: opt.emSenderEmail,
            srcPW: opt.emSenderPW,
            emTitle,
            emContent,
            toEmails,
            toEmailsCC,
            toEmailsBCC,
        }

        //WEmail
        return new WEmail(optEmail)
    }


    /**
     * email
     *
     * @param {String} emTitle 
     * @param {String} emContent 
     * @param {String|Array} toEmails email
     * @param {String|Array} [toEmailsCC=[]] email[]
     * @param {String|Array} [toEmailsBCC=[]] email[]
     * @returns {Promise} Promiseresolvereject
     */
    function sendEmailByLetter(emTitle, emContent, toEmails, toEmailsCC, toEmailsBCC) {

        //tmp
        let tmp = opt.emLetterHtml

        //webName, webUrl, emLetterLinks
        let webName = opt.webName
        let webUrl = opt.webUrl
        let emLetterLinks = opt.emLetterLinks
        if (webName !== '' && webUrl !== '') {
            emLetterLinks = concat({ name: webName, url: webUrl }, emLetterLinks)
        }
        emLetterLinks = map(emLetterLinks, function(v) {
            let name = get(v, 'name', '')
            let url = get(v, 'url', '')
            return `<a style="text-decoration:none" href="${url}" target="_blank" >${name}</a>`
        })
        emLetterLinks = `| ${join(emLetterLinks, ' | ')} |`

        //replaceObj
        tmp = replaceObj(tmp, {
            '{emMessage}': emContent,
            '{emWebName}': webName,
            '{emWebUrl}': webUrl,
            '{emWebDescription}': opt.webDescription,
            '{emLetterDoNotReplayMessage}': opt.emLetterDoNotReplayMessage,
            '{emLetterTeamMessage}': opt.emLetterTeamMessage,
            '{emLetterLinks}': emLetterLinks,
        })

        //sendEmail
        return sendEmail(emTitle, tmp, toEmails, toEmailsCC, toEmailsBCC)
    }


    function sendEmailWhenSignUp(name, urlCheckCode, toEmails) {

        //emTitle
        let emTitle = opt.emSignUpTitle

        //tmp
        let tmp = opt.emSignUpHtml

        //replaceObj
        tmp = replaceObj(tmp, {
            '{name}': name,
            '{urlCheckCode}': urlCheckCode,
        })

        return sendEmailByLetter(emTitle, tmp, toEmails, [], opt.emBCC)
    }
    let sendEmailWhenSignUpR = pm2resolve(sendEmailWhenSignUp)


    function sendEmailWhenResetPW(name, urlCheckCode, toEmails) {

        //emTitle
        let emTitle = opt.emResetPWTitle

        //tmp
        let tmp = opt.emResetPWHtml

        //replaceObj
        tmp = replaceObj(tmp, {
            '{name}': name,
            '{urlCheckCode}': urlCheckCode,
        })

        return sendEmailByLetter(emTitle, tmp, toEmails, [], opt.emBCC)
    }
    let sendEmailWhenResetPWR = pm2resolve(sendEmailWhenResetPW)


    //funcs
    let funcs = {


        /**
         * 
         *
         * @memberof WUserServer
         * @function select
         * @param {Object} inp token
         * @param {String} inp.token token
         * @param {Object} [inp.find={}] {}
         * @returns {Promise} Promiseresolvestate'success'msgstate'error'msgreject
         */
        'select': function(inp) {
            return pmWithEmit(selectPublicR, 'selectPublic')(inp)
        },


        /**
         * 
         *
         * 1.emailemail
         *
         * 2.(activeactiveYes)
         *
         * 3.email
         *
         * 4.(activeactiveYes)
         *
         * @memberof WUserServer
         * @function signUp
         * @param {Object} user 
         * @param {String} user.email email
         * @param {String} user.pwEnc 
         * @param {String} [user.address=''] 
         * @param {String} [user.phone=''] 
         * @param {String} [user.organization=''] 
         * @param {String} [user.position=''] 
         * @returns {Promise} Promiseresolvestate'success'msgidstate'error'msgreject
         */
        'signUp': function(user) {
            return pmWithEmit(signUpR, 'signUp')(user)
        },


        /**
         * 
         *
         * 1.email(activeactiveYes)
         *
         * 2.
         *
         * 3.email
         *
         * 4.email
         *
         * 5.tokentokentoken
         *
         * @memberof WUserServer
         * @function logIn
         * @param {Object} user 
         * @param {String} user.email email
         * @param {String} user.pwEnc 
         * @returns {Promise} Promiseresolvestate'success'msgtokenstate'error'msgreject
         */
        'logIn': function(user) {
            return pmWithEmit(logInR, 'logIn')(user)
        },


        /**
         * token
         *
         * 1.token
         *
         * @memberof WUserServer
         * @function refreshTokenExp
         * @param {String} token token
         * @returns {Promise} Promiseresolvestate'success'state'error'msgreject
         */
        'refreshTokenExp': function(token) {
            return pmWithEmit(refreshTokenExpR, 'refreshTokenExp')(token)
        },


        /**
         * token
         *
         * 1.token
         *
         * @memberof WUserServer
         * @function isValidToken
         * @param {String} token token
         * @returns {Promise} Promiseresolvestate'success'state'error'msgreject
         */
        'isValidToken': function(token) {
            return pmWithEmit(isValidTokenR, 'isValidToken')(token)
        },


        /**
         * 
         *
         * 1.token
         *
         * @memberof WUserServer
         * @function logOut
         * @param {String} token token
         * @returns {Promise} Promiseresolvestate'success'state'error'msgreject
         */
        'logOut': function(token) {
            return pmWithEmit(logOutR, 'logOut')(token)
        },


        /**
         * 
         *
         * 1.(activeactiveYes)
         *
         * 2.
         *
         * @memberof WUserServer
         * @function changePW
         * @param {Object} user 
         * @param {String} user.email email
         * @param {String} user.pwEnc 
         * @returns {Promise} Promiseresolvestate'success'state'error'msgreject
         */
        'changePW': function(user) {
            return pmWithEmit(changePWR, 'changePW')(user)
        },


        /**
         * 
         *
         * 1.(activeactiveYes)
         *
         * 2.
         *
         * 2.
         *
         * @memberof WUserServer
         * @function resetPW
         * @param {Object} user 
         * @param {String} user.email email
         * @param {String} user.pwEncNew 
         * @returns {Promise} Promiseresolvestate'success'state'error'msgreject
         */
        'resetPW': function(user) {
            return pmWithEmit(resetPWR, 'resetPW')(user)
        },


        /**
         * 
         *
         * @memberof WUserServer
         * @function modifyInfor
         * @param {Object} inp token
         * @param {String} inp.token token
         * @param {Object} [inp.user={}] {}
         * @returns {Promise} Promiseresolvestate'success'state'error'msgreject
         */
        'modifyInfor': function(inp) {
            return pmWithEmit(modifyInforR, 'modifyInfor')(inp)
        },


    }
    each(opt.funcs, function(v, k) {
        if (haskey(funcs, k)) {
            console.log('invalid func: ' + k)
        }
        else {
            funcs[k] = opt.funcs[k]
        }
    })


    //routes
    let routes = [
        {
            method: 'GET',
            path: '/auth',
            handler: async function (req, res) {
                //console.log(`Server[port:${opt.port}][api:auth]:`, 'checkCode: ' + req.query.checkCode)

                //checkCode
                let checkCode = req.query.checkCode

                /**
                 * 
                 *
                 * 1.
                 *
                 * 2.html
                 *
                 * @memberof WUserServer::Private
                 * @function verifyCheckCodeAndGetHtml
                 * @param {String} checkCode
                 * @returns {Promise} Promiseresolvehtmlreject
                 */
                return verifyCheckCodeAndGetHtml(checkCode)
            }
        },
    ]
    each(opt.routes, function(v) {
        routes.push(v)
    })


    //merge opt
    opt = {
        ...opt,
        // filterFuncs: function(token, funcs) {
        //     return new Promise(function(resolve, reject) {
        //         funcs = funcs.filter(function(v) {
        //             return v.indexOf('Hide') < 0
        //         })
        //         resolve(funcs)
        //     })
        // },
        onClientChange: function(clients, opt) {
            //console.log(`Server[port:${opt.port}] now clients: ${clients.length}`)
            eeEmit('clientChange', clients.length, clients)
        },
        funcs: funcs,
        routes: routes,
    }


    //new
    new HtServer(opt)


    return ee
}


export default WUserServer
