import each from 'lodash/each'
import isstr from './isstr.mjs'
import isestr from './isestr.mjs'
import isearr from './isearr.mjs'


/**
 * skeys
 *
 * Unit Test: {@link https://github.com/yuda-lyu/wsemi/blob/master/test/binstr.test.js Github}
 * @memberOf wsemi
 * @param {String} s 
 * @param {Array|String} keys 
 * @param {String} mode 'anyone''all''anyone'
 * @returns {Boolean} 
 * @example
 * binstr('1.25abc', 'ab')
 * // => true
 */
function binstr(s, keys, mode = 'anyone') {

    //check
    if (!isestr(s)) {
        return false
    }

    //check mode
    if (mode !== 'anyone' && mode !== 'all') {
        return false
    }

    //keys
    if (isstr(keys)) {
        if (isestr(keys)) {
            keys = [keys]
        }
        else {
            return false
        }
    }

    //keys
    if (!isearr(keys)) {
        return false
    }

    //check keys
    let bNotStr = false
    each(keys, function(key) {
        if (!isestr(key)) {
            bNotStr = true
        }
    })
    if (bNotStr) {
        return false
    }

    //instr
    function instr(s, key) {
        return s.indexOf(key)
    }

    //keys
    let n = 0
    each(keys, function(key) {
        if (instr(s, key) !== -1) {
            n++
        }
    })

    if (mode === 'anyone') {
        return n > 0
    }
    else if (mode === 'all') {
        return n === keys.length
    }
    return false
}


export default binstr
